import React, { useCallback, useContext } from "react";
import { Col, Row, Button } from 'reactstrap';
import { withRouter, Redirect } from 'react-router-dom';
import app from "../../firebase_config";
import { AuthContext } from "../../Auth";

const hichLogo = require('../../assets/images/hich-logo.png');

const permittedUsers = [
  'nwaughac@gmail.com', 
  'routonmh@dukes.jmu.edu', 
  'drkhoi15@hotmail.com',
  'zachcasey10@gmail.com',
  'trustand11@gmail.com'
];

const Login = ({ history }) => {
  const handleLogin = useCallback(
    async event => {
      event.preventDefault();
      const { email, password } = event.target.elements;
      if (!permittedUsers.includes(email.value)) {
        alert('You are not able to login. Please contact developer@hich.app for access.');
        return;
      }
      try {
        await app
          .auth()
          .signInWithEmailAndPassword(email.value, password.value);
        history.push("/");
      } catch (error) {
        alert(error);
      }
    },
    [history]
  );

  const { currentUser } = useContext(AuthContext);

  if (currentUser) {
    return <Redirect to="/dashboard" />;
  }

  return (

    <div className="login-wrap">

      <Row className="row align-items-center left-inner-wrap">

        <Col md={6} className="login-left">

          <img className="login-logo" src={hichLogo} alt="Hich Logo" />

          <h4 className="login-header">Welcome!</h4>

          <h5 className="login-header">Hich App Dashboard</h5>

          <h6 className="login-header">Admin Login</h6>

          <form onSubmit={handleLogin}>

            <input className="form-fields" name="email" type="email" placeholder="Email Address" />

            <input className="form-fields" name="password" type="password" placeholder="Password" />

            <Button type="submit" color="info" className="login-btn">Log in</Button>
          </form>

        </Col>

      </Row>
      <Col md={6} className="login-right"></Col>
    </div>
  );
};

export default withRouter(Login);
